import './App.css';
import NavScrollExample from './Components/Navigation/Nav.js'
import 'bootstrap/dist/css/bootstrap.min.css';
import Home from './Components/Home.js';
import Projects from './Components/Projects';
import Blog from './Components/Blog';
import Footer from './Components/Footer';
import Quote from './Components/Quote';
import Menu from './Components/Menu'
import Wave from './Components/Wave'
import InstagramLike from './Components/InstagramLike'
import MixAndRideContainer from './Components/MixRide/MixAndRideContainer';

import {
  HashRouter as Router,
  Routes,
  Route,
  Link,
  BrowserRouter,
  HashRouter,
} from 'react-router-dom';


function App() {
  return (
    <BrowserRouter basename='/personal_website'>
        <div className="App">
        <NavScrollExample />
          <Routes>
                 <Route exact path='/' element={
                 <>
                  <Home />
                  <Quote />
                  <Menu />

                 </>
                }></Route>
                 <Route exact path='/blog' element={< Blog />}></Route>
                 <Route exact path='/professional' element={< Projects />}></Route>
                 <Route exact path='/modelling_portfolio' element={<InstagramLike />}></Route>
                 <Route exact path='/mix_and_ride' element={<MixAndRideContainer/>}></Route>
          </Routes>
          <Wave/>
          <Footer/>
      </div>
    </BrowserRouter>
  );
}
export default App;


